import tweet from "../kuvat/twitter.png"
import ig from "../kuvat/ig.png"

export default function Yhteys () {
	return (
		<div class="sivu"> 
        <article>
            <div>
                <div>
                    
                    <h1>Ota yhteyttä</h1>
                    
                    <p>
                    Tekijä saattoi mokata, kävijä saattaa haluta hehkuttaa kuinka hyvin sivusto onnistui. Mikä lie olikaan syy yhteydenotolle, tekijän saa kiinni seuraavilla tavoilla:
                    </p>
                    <p>Sähköposti: keijukoiso@gmail.com</p>
                    <p>Sosiaalinen media, klikkaa kuvaa:</p>
                    
                    <p>
                        <a href="https://www.instagram.com/keijukoiso/"><img src={ig} alt="@Keijukoiso on instagram" id="somelink"/></a>
                        <a href="https://twitter.com/keijukoiso"><img src={tweet} alt="Keijukoiso on twitter" id="somelink"/></a>
                    </p>
                    
                </div>
            </div>
        </article>
    </div>
);
}  