import { useState } from 'react';
import './Laskuri.css'

export default function Laskuri () {
    const [mat, setMat] = useState([]);
    const [materiaali, setMateriaali] = useState("");
    const [hinta, setHinta] = useState("");
    const [maara, setMaara] = useState("");
    const [kerroin, setKerroin] = useState("");
    const [yht, setYht] = useState("");

    

    const mats = (e) => {
        setMateriaali(e.target.value);
    };

    const hintaa = (e) => {
        setHinta(e.target.value);
    };

    const maarat = (e) => {
        setMaara(e.target.value);
    };

    const kerrointa = (e) => {
        setKerroin(e.target.value);
    };

    const lisaaMat = () => {
        let temp = mat;
        let tulo = yht;
        tulo += 1;
        if (materiaali.length > 0 && maara > 0) {
            temp.push({ materiaali: materiaali, hinta: hinta, maara: maara });
            setMat(temp);
            setYht(tulo);
            setMateriaali("");
            setMaara("");
            setHinta("");
        }
    
    };

    const tyhjenna = () => {
        setMateriaali("");
        setMaara("");
        setMat([]);
        setHinta("");
        setYht("");
        setKerroin("");
      };

    
      
  
    return (
        <div class="sivu">
            <h1>Laskuri</h1>
            <p>
                Laskuri materiaaleille x määrään craftikertoja ja niiden hinta. 
            </p>
        
        
        <div class="Laskin1">
            <div class="d1">
                <label for="kerro" class="kerro">Craftikertojen määrä</label>
                <input type="number" placeholder="0" value={kerroin} onChange={(e) => kerrointa(e)}></input>
            </div>
                    

            <div class="d1">
                <label for="matNimi" class="mat-Nimi">Materiaalin nimi</label>
                <input type="text" value={materiaali} onChange={(e) => mats(e)}></input>

                <label for="hinta" class="mat-hinta">Hinta</label>
                <input type="number" placeholder="0" value={hinta} onChange={(e) => hintaa(e)}></input>

                <label for="maara" class="maara">Määrä yhteen craftiin</label>
                <input type="number" placeholder="0" value={maara} onChange={(e) => maarat(e)}></input>

                <button onClick={() => lisaaMat()}>Lisää</button>
                
            </div>
        </div>
      
        <div class="Laskin2">
            <table id="tLaskin">
                <thead>
                    <tr>
                        <th>Resepti</th>
                        <th>Tarvitaan</th>
                        <th>Hinta</th>
                    </tr>
                </thead>
                <Materiaalit data={mat} k={kerroin} />
                <tr>
                    <td></td>
                    <td></td>
                    <td>{yht}</td>
                </tr>
            </table>
            <div class="d2">
                <button class="tyhjenna" onClick={() => tyhjenna()}>Tyhjennä</button>
            </div>
        </div>
      
      
    </div>
    )

    
};

const Materiaalit = (props) => {
    const data = props.data.map((x, i) => {
        let kerrottu = x.maara * props.k
        let hinta = x.hinta * props.k
        return <tr key={i}>
            <td>{x.maara} x {x.materiaali}</td>
            <td>{kerrottu}</td>
            <td>{hinta}</td>
        </tr>;
    }) ;
        
    return (
            <tbody>
                {data}
            </tbody>
            
    )
};


         